import axios from 'axios'
export default {
  layout: "download",
  data () {
    return {
      getSysInfoData: {
        androidVersion: "",
        iosDownload: "",
        androidDownload: "",
      }
    }
  },
  created () {
    this.getSysInfo()
  },
  methods: {
    getSysInfo () {
      let data = {};
      axios.post(`https://nbex.jcpt.club/api/main/getSysInfo`,data)
      .then(res=>{
        if (res.data.type == "success") {
          this.getSysInfoData = res.data.data
        }else{
          this.$Toast(res.data.message)
        }
      })
    }
  },
}
